import React, {useState, useEffect} from 'react'
import {connect} from 'react-redux'
import {selectedPageAction} from '../../../actions'
function ProductDropDownMenu(props) {
    const [activePage, setActivePage] = useState(props.selectedProductPage)
    useEffect(()=>{
        setActivePage(props.selectedProductPage)
    },[props.selectedProductPage])
    return (
        <div className='outer-div-dropDown'>
            <div className={activePage === 'nbromosuccinimide' ? 'product-list-style background-color-drop-down-option':'product-list-style'} onClick={()=>props.selectedPageAction('nbromosuccinimide')}>N-Bromosuccinimide</div>
            <div className={activePage === 'nchlorosuccinimide' ? 'product-list-style background-color-drop-down-option':'product-list-style'} onClick={()=>props.selectedPageAction('nchlorosuccinimide')}>N-Chlorosuccinimide</div>
            <div className={activePage === 'coppercynide' ? 'product-list-style background-color-drop-down-option':'product-list-style'} onClick={()=>props.selectedPageAction('coppercynide')}>Copper(I) cyanide</div>
        </div>
    )
}

const mapStateToProps = (state) => {
	console.log("initial path drop down:", state.selectedProductPage);
	return {
		selectedProductPage: state.selectedProductPage,
	};
};

export default connect(mapStateToProps,{selectedPageAction})(ProductDropDownMenu)
